<template>
    <div id="qr-code">
      <div id='code'>请使用{{chooseWay === 'aliChoose'?'支付宝':'微信'}}扫码支付</div>
      <div class="canvas-wrap">
        <canvas id="canvas"></canvas>
      </div>
    </div>
</template>
<style></style>
<script>
  import QRCode from 'qrcode'
  import axios from 'axios'
  import Qs from 'qs'
  import {postPolling} from '@/api/api.js'
    export default{
      props: {
        qr_code: {
          type: String,
          default: ''
        },
        amount: {
          type: Number,
          default: 0
        },
        chooseWay: {
          type: String,
          default: 'aliChoose'
        },
        orderId: {
          type: String,
          default: ''
        }
      },
      // props: ['qr_code','amount','chooseWay'],
      data(){
            return {
              codes:'',
              times: 100,
              timeId: 0
            }
      },
      components: {
        QRCode: QRCode
      },

      methods: {
        pollingOrder () {
          postPolling({
            orderid: this.orderId
          }).then(res => {
            if (res.data.code === 'success') {
              console.log(`orderid:${this.orderId};success`)
              clearTimeout(this.timeId);
              this.$alert('支付成功', '提示', {
                  confirmButtonText: '确定',
                  type: 'success',
                      callback: () => {
                          this.$router.push('/');
                      }
              }); 
              this.$router.push({
                path: '/'
              })
            } else if (res.data.code === 'waiting') {
              console.log(`orderid:${this.orderId};waiting`)
              this.times -= 1;
              if (this.times > 0) {
                this.timeId = setTimeout(this.pollingOrder, 3000)
              }
            } else {

            }
          },err => {
            console.log(err)
          })
        },
        useqrcode(){
          if (this.qr_code) {
            var canvas = document.getElementById('canvas')
            QRCode.toCanvas(canvas, this.qr_code, function (error) {
              if (error) {
                console.error(error)
              } else {
                this.timeId = setTimeout(this.pollingOrder,3000)
              }
            })

          }
        }
      },
      created () {
          console.log('qr_code',this.qr_code);
          console.log('amount',this.amount);
      },
      mounted(){
            this.useqrcode();
      }
    }
</script>

<style lang="less">
#qr-code{
    margin: 0 auto;
    padding-top: 200px;
    div{
        text-align: center;
        font-size: 38px; /*px*/
        margin-bottom: 40px;
    }
    .canvas-wrap{
        margin: 0 auto;
        width: 328px; /*px*/
        height: 328px; /*px*/
    }
}
</style>



